@import url(https://fonts.googleapis.com/css2?family=Prompt:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
@import url(https://fonts.googleapis.com/css?family=Prompt:100,100italic,200,200italic,300,300italic,regular,italic,500,500italic,600,600italic,700,700italic,800,800italic,900,900italic);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

button.btn{cursor:pointer}button.btn:hover{-webkit-filter:brightness(0.9);filter:brightness(0.9)}

.btn.btn-default{background-color:#f8f9fa;border:none;border-radius:8px;box-shadow:0px 0px 10px rgba(0,0,0,0.1);padding:8px 12px}

.btn.btn-primary{background-color:#097ff5;border:none;border-radius:8px;box-shadow:0px 0px 10px rgba(0,0,0,0.1);padding:8px 12px;color:#ffffff;text-shadow:0 1px 0 rgba(255,255,255,0)}

.input-group{display:flex;flex:1 1;width:100%;justify-content:stretch;align-items:stretch;min-height:40px;margin-top:5px;margin-bottom:15px}.input-group:hover{-webkit-filter:brightness(0.98);filter:brightness(0.98)}.input-group span{display:flex;justify-content:center;align-items:center;flex:1 1;min-width:45px;padding:0 3px;background-color:#fdfdfd;border:none;border-radius:8px;box-shadow:-3px 0px 10px rgba(0,0,0,0.1);z-index:99}.input-group input.form-control{flex:1 1;border:none;padding:0 0 0 8px;border-radius:8px;box-shadow:0px 0px 10px rgba(0,0,0,0.1)}.input-group input.form-control:focus{outline:none}.input-group.inline{flex-direction:row}.input-group.inline span{flex:2 1;border-right:none;border-top-right-radius:0;border-bottom-right-radius:0}.input-group.inline input.form-control{flex:16 1;border-top-left-radius:0;border-bottom-left-radius:0}.input-group button.form-control{width:100%;font-size:17px;font-family:"Prompt", sans-serif;text-transform:capitalize;font-weight:600}main{display:flex;flex:1 1;height:100vh;align-items:stretch;flex-direction:row}main aside{transition:0.2s linear;display:flex;flex:10 1;justify-content:center;align-items:center;background-color:#e32432;flex-direction:column}main aside h1{display:inline-block;margin:0;font-family:"Prompt", sans-serif;font-style:italic;font-weight:600;font-size:3.6em;color:#fff}main aside a{display:block;color:#000;text-decoration:none}main div.content{transition:0.2s linear;display:flex;flex:1 1;justify-content:stretch;align-items:stretch;background-color:#f8f9fa}main div.content:hover{transition:0.2s linear;flex:20 1;box-shadow:-4px 0px 15px rgba(0,0,0,0.3);z-index:999}main div.content .content-indicate{transition:0.2s linear;flex:3 1;display:flex;justify-content:center;align-items:center;flex-direction:column}main div.content .content-indicate.arrow{padding:35px 0 0 0}main div.content .content-indicate.arrow span{margin-top:15px}main div.content .content-indicate span{font-size:18px}main div.content .content-container{transition-delay:3s;display:none;flex:14 1;justify-content:stretch;align-items:center;padding:0 15px;opacity:0}main div.content .content-container .formContent,main div.content .content-container .formContent form{display:flex;flex:1 1;justify-content:stretch;align-items:center;flex-direction:column}main div.content .content-container .formContent .formContent-area,main div.content .content-container .formContent form .formContent-area{flex:1 1;width:100%}main div.content .content-container .formContent .formContent-area.formContent-header,main div.content .content-container .formContent form .formContent-area.formContent-header{text-align:center;padding:15px 0}main div.content .content-container .formContent .formContent-area.formContent-header h1,main div.content .content-container .formContent form .formContent-area.formContent-header h1{display:inline-block;margin:0;font-family:"Roboto", sans-serif;font-weight:600;font-size:2.2em}main div.content .content-container .formContent form{max-width:80%;width:300px}main div.content:hover .content-container{transition-delay:3s;transition:3s linear;opacity:1;display:flex}

div.queroConhecer-content{display:flex;justify-self:center;align-items:center;flex-direction:column;grid-gap:25px;gap:25px;padding:0 25px 0 15px}div.queroConhecer-content h1{font-size:25px}main>aside:hover div.queroConhecer-content{flex-direction:row}main>aside:hover div.queroConhecer-content h1{font-size:45px}main>aside:hover div.queroConhecer-content iframe{width:280px;height:673px}main>aside div.queroConhecer-content iframe{width:280px;height:544px;max-width:90%;max-height:90%;border-radius:10px;box-shadow:0px 0px 20px rgba(0,0,0,0.1)}

body{background-color:#e32432}body header{flex:1 1;background-color:rgba(0,0,0,0.1);padding:5px 15px}body .container{display:flex;align-items:center;padding:0 15px;max-width:calc(100% - 30px);width:1366px;justify-content:space-between}body header .logo,body header>.container .logo{font-family:"Prompt", sans-serif;font-style:italic;font-weight:bold;font-size:1.5em;color:#fff;text-shadow:0px 0px 10px rgba(0,0,0,0.1)}body header ul,body header>.container ul{display:flex;justify-content:flex-end;align-items:center;list-style:none;padding:0;margin:0}body header ul li,body header>.container ul li{display:inline-block}body header ul li a,body header>.container ul li a{display:block}body header ul li a.imageArea,body header>.container ul li a.imageArea{padding:2px;border-radius:100%;border:2px solid #f9f8fa}body header ul li a img,body header>.container ul li a img{width:2.8rem;height:2.8rem;border-radius:100%}body header ul li.dropdown-float>ul,body header>.container ul li.dropdown-float>ul{position:absolute;height:350px;border-radius:10px;margin-top:5px;right:15px;background-color:#ffffff;display:block;width:200px}body header ul li.dropdown-float>ul li,body header>.container ul li.dropdown-float>ul li{display:inline-block;border-bottom:1px solid rgba(0,0,0,0.3)}body header ul li.dropdown-float>ul li a,body header>.container ul li.dropdown-float>ul li a{display:flex;padding:8px;justify-content:space-between;align-items:center;flex-direction:row}

